

.footer-right {
    z-index: 100;
    color: white;
    margin-right: 15px;
}

.sidebar-icon {
    width: 50px;
    display: flex;
    margin-bottom: 30px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.record {
    animation: 5s infinite spinTheRecord;
    filter: invert(1);
}

@keyframes spinTheRecord {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.record-below {
    margin-bottom: 70px;
}